export async function getHRDashboardData() {
    const employeeCount = accounts.filter(a => a.role === 'Employee' && a.status === 'Active').length;
    const pendingLeaves = leave_requests.filter(lr => lr.status === 'Pending').length;
    const recentActivities = leave_requests
        .sort((a, b) => new Date(b.start_date) - new Date(a.start_date))
        .slice(0, 5)
        .map(lr => {
            const employee = accounts.find(a => a.id === lr.account_id);
            return {
                id: lr.id,
                activity: `${employee.first_name} ${employee.last_name} requested ${lr.leave_type.toLowerCase()} leave`,
                timestamp: formatDistanceToNow(new Date(lr.start_date), { addSuffix: true }),
                type: 'Leave'
            };
        });
    
    return {
        stats: [
            { title: 'Total Employees', value: employeeCount, icon: 'Users', change: '+2 this month' },
            { title: 'On Time Percentage', value: '96%', icon: 'CheckCircle', change: '-1% from last month' },
            { title: 'Active Job Postings', value: '4', icon: 'Briefcase', change: '+1 new posting' },
            { title: 'Pending Leave Requests', value: pendingLeaves, icon: 'Bell', change: `${pendingLeaves} new today` },
        ],
        recentActivities
    };
}


export async function getPastAnnouncements() {
    return announcements.map(a => {
        const postedBy = accounts.find(acc => acc.id === a.posted_by_id);
        return {
            ...a,
            postedBy: `${postedBy.first_name} ${postedBy.last_name}`,
            date: new Date(a.posted_at).toISOString().split('T')[0]
        };
    }).sort((a, b) => new Date(b.date) - new Date(a.date));
}

export async function getDailyAttendanceData(date) {
    return accounts
        .filter(acc => acc.role === 'Employee' && acc.status === 'Active')
        .map(acc => {
            const attendanceRecord = attendance.find(att => att.account_id === acc.id && new Date(att.attendance_date).toISOString().split('T')[0] === date);
            return {
                id: acc.id,
                name: `${acc.first_name} ${acc.last_name}`,
                position: acc.position,
                branch: acc.branch,
                timeIn: attendanceRecord?.time_in || '--',
                timeOut: attendanceRecord?.time_out || '--',
                status: attendanceRecord?.status || 'Absent'
            };
        });
}


export async function getEmployees() {
    return accounts
        .filter(acc => acc.role === 'Employee')
        .map(acc => ({
            id: acc.id,
            name: `${acc.first_name} ${acc.last_name}`,
            position: acc.position,
            branch: acc.branch,
            status: acc.status
        }));
}


export async function getLeaveRequests() {
    const statusOrder = { 'Pending': 1, 'Approved': 2, 'Rejected': 3 };
    return leave_requests.map(lr => {
        const employee = accounts.find(a => a.id === lr.account_id);
        return {
            id: lr.id,
            employeeName: `${employee.first_name} ${employee.last_name}`,
            employeeId: employee.id,
            leave_type: lr.leave_type,
            startDate: lr.start_date,
            endDate: lr.end_date,
            reason: lr.reason,
            status: lr.status
        };
    }).sort((a, b) => {
        const order = statusOrder[a.status] - statusOrder[b.status];
        if (order !== 0) return order;
        return new Date(a.startDate) - new Date(b.startDate);
    });
}


export async function getEmployeesWithPayslipStatus() {
     return accounts
        .filter(acc => acc.role === 'Employee' && acc.status === 'Active')
        .map(acc => {
            const hasPayslip = payslips.some(p => p.account_id === acc.id);
            return {
                id: acc.id,
                name: `${acc.first_name} ${acc.last_name}`,
                position: acc.position,
                department: acc.department,
                payslipStatus: hasPayslip ? 'Generated' : 'Pending'
            };
        });
}


