export async function getEmployeeDashboardData(employeeId) {
    const employeeData = accounts.find(acc => acc.id === employeeId);
    const pubAnnouncements = announcements
        .filter(a => a.status === 'Published')
        .map(a => ({...a, date: new Date(a.posted_at).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric'})}))
        .sort((a, b) => new Date(b.posted_at) - new Date(a.posted_at))
        .slice(0, 5);
        
    return {
        employee: {
            employeeId: employeeData.id,
            name: `${employeeData.first_name} ${employeeData.last_name}`,
            position: employeeData.position,
            department: employeeData.department,
            email: employeeData.email,
        },
        announcements: pubAnnouncements
    };
}


export async function getAttendanceData(employeeId) {
    const employeeRecords = attendance
        .filter(a => a.account_id === employeeId)
        .map(r => ({
            date: new Date(r.attendance_date).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric'}),
            timeIn: r.time_in,
            timeOut: r.time_out,
            status: r.status
        }));
    
    const daysAttended = employeeRecords.filter(r => r.status === 'Present' || r.status === 'Late').length;
    const lates = employeeRecords.filter(r => r.status === 'Late').length;
    const absences = employeeRecords.filter(r => r.status === 'Absent').length;
    const totalHours = daysAttended * 8; // simplified calculation
    const availableLeaves = accounts.find(a => a.id === employeeId)?.available_leaves || 0;
    
    return {
        summary: { daysAttended, lates, absences, totalHours, availableLeaves },
        records: employeeRecords
    };
}


export async function getPastLeaveRequests(employeeId) {
    return leave_requests
        .filter(lr => lr.account_id === employeeId)
        .map(lr => ({
            id: lr.id,
            leave_type: lr.leave_type,
            startDate: lr.start_date,
            endDate: lr.end_date,
            status: lr.status
        }))
        .sort((a, b) => new Date(b.startDate) - new Date(a.startDate));
}


export async function createLeaveRequest(leaveRequest) {
    const newId = Math.max(...leave_requests.map(lr => lr.id), 0) + 1;
    leave_requests.push({
        id: newId,
        account_id: leaveRequest.employeeId,
        leave_type: leaveRequest.leaveType,
        start_date: leaveRequest.startDate,
        end_date: leaveRequest.endDate,
        reason: leaveRequest.reason,
        status: 'Pending'
    });
}


export async function getPayPeriods(employeeId) {
    return payslips
        .filter(p => p.account_id === employeeId)
        .map(p => ({
            ...p,
            period: `${new Date(p.period_start).toLocaleDateString()} - ${new Date(p.period_end).toLocaleDateString()}`,
            payDate: new Date(p.pay_date).toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' })
        }))
        .sort((a, b) => new Date(b.period_start) - new Date(a.period_start));
}


