export async function approveEmployee(employeeId) {
    const account = accounts.find(acc => acc.id === employeeId);
    if (account) {
        account.status = 'Active';
    }
}


export async function rejectEmployee(employeeId) {
    const index = accounts.findIndex(acc => acc.id === employeeId);
    if (index !== -1) {
        accounts.splice(index, 1);
    }
}

export async function findUserByEmail(email) {
    return accounts.find(user => user.email === email);
}

export async function addEmployee(employeeData) {
    const newId = `EMP-${String(Date.now()).slice(-5)}`;
    const existing = accounts.some(acc => acc.email === employeeData.email);
    if (existing) {
        throw new Error("An employee with this email already exists.");
    }
    accounts.push({
        id: newId,
        first_name: employeeData.firstName,
        last_name: employeeData.lastName,
        middle_name: employeeData.middleName,
        email: employeeData.email,
        password: 'password', // In a real app, generate a secure temp password
        role: 'Employee',
        position: employeeData.position,
        department: employeeData.department,
        branch: employeeData.branch,
        gender: employeeData.gender,
        date_of_birth: employeeData.dob,
        date_hired: employeeData.hireDate,
        status: 'Pending Approval',
        available_leaves: 15,
    });
}
